// in src/Dashboard.js
import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
// import SimpleLineChart from "../components/linecharts";
// import CustomShapeBarChart from "../components/barcharts";
// import CustomShapeBarChartss from "../components/barchartss";
// import Randomcharts from "../components/randomcharts";
//import { TextField, Datagrid } from "react-admin";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    fontWeight: "Bold",
    color: theme.palette.text.secondary
  },
  pont: {
    fontSize: "30px",
    fontWeight: "Bold",
    padding: theme.spacing.unit * 2,
    textAlign: "center"
  },
  line: {
    position: "right"
  }
});

function MyAccount(props) {
  const { classes } = props;
  return (
    <div className={classes.root}>
      <Grid container spacing={24}>
        <Grid item xs={6} md={7}>
          <Paper className={classes.pont}>Detail MyAccount </Paper>
        </Grid>

        <Grid item xs={6} md={4}>
          <Paper className={classes.pont}>MyAccount </Paper>
        </Grid>
        <Grid item xs={12} md={12}>
          <Paper className={classes.pont}>
            Info Grafis Bank Sampah Perjuangan{" "}
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}

MyAccount.PropTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(MyAccount);
